import request from './request';
import { SET_ERROR } from '../../upload-reducer';
export const createXhrClient = ({
  baseUrl,
  modifyRequest
}) => async ({
  dispatch,
  onProgress,
  files,
  options
}) => {
  let modifiedOptions = modifyRequest ? modifyRequest(options) : options;
  let url = `${baseUrl}${options.path || ''}`; //Get the url using a promise, for signed uploads
  // this has grown out of control... i need to refactor this library since it was my first time really using TS
  // and i had taken old upload code

  if (modifiedOptions.getUrl) {
    try {
      let response = await modifiedOptions.getUrl(files);
      if (typeof response === 'string') url = response;else {
        url = response.url;
        if (response.headers) modifiedOptions = { ...modifiedOptions,
          headers: response.headers
        };
        if (response.fields) modifiedOptions = { ...modifiedOptions,
          fields: response.fields
        };
      }
    } catch (error) {
      console.error(error); // If there was a problem, set an error

      return dispatch({
        type: SET_ERROR,
        payload: 'Error getting async upload url'
      });
    }
  }

  return request({
    options: modifiedOptions,
    url,
    files,
    onProgress
  });
};